* {
  box-sizing: border-box;
}

.table {
  border: solid 1px silver;
}
.table-header {
  display: flex;
  // border: solid 1px silver;
  border-bottom: solid 1px silver;
}
.table-header-days {
  display: flex;
}
.table-header-column {
  min-width: 25px;
}
.table-body-inner {
  display: flex;
}
.table-body-inner:not(:last-of-type) {
  display: flex;
  border-bottom: solid 1px silver;
}
.table-body-row-wrapper {
  display: flex;
  // border: solid 1px silver;
}
.table-body-row {
  display: flex;
  position: relative;
  height: 50px;
  width: 100%;
  // border: solid 1px silver;
}

.task {
  min-width: 150px;
  border-right: solid 1px silver;
  display: flex;
  align-items: center;
  justify-content: center;
}
.start-at,
.end-at {
  min-width: 100px;
  border-right: solid 1px silver;
  display: flex;
  align-items: center;
  justify-content: center;
}

.rnd {
  border: 2px solid red;
  background: orange;
}
